.Nav-Container {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4% 8%;
}

.Nav-Container .Logo {
  width: 135px;
  height: 135px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.Nav-Container .Logo .ham {
  display: none;
  width: 100%;
  height: auto;
}

.Nav-Container .Logo .logo_img {
  width: 100%;
  height: 100%;
}

.Nav-Container .navbar {
  overflow: hidden;
  animation: navAnim 0.8s ease-out;
  transition: 0.4s ease-out;
}

.Nav-Container .open {
  overflow: hidden;
  animation: navAnim 0.8s ease-out;
  transition: 0.4s ease-out;
}

@keyframes navAnim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.Nav-Container .open .nav-links {
  float: left;
  font-size: 10px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  color: gray;
  transition: 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.Nav-Container .navbar .nav-links {
  float: left;
  font-size: 10px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  color: gray;
  transition: 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.Nav-Container .dropdown {
  float: left;
  overflow: hidden;
}

.Nav-Container .dropdown .dropbtn {
  cursor: pointer;
  font-size: 10px;
  border: none;
  outline: none;
  color: gray;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
  transition: 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.Nav-Container .navbar .nav-links:hover,
.Nav-Container .dropdown:hover .dropbtn {
  color: white;
  background-color: #3685c3;
}

.Nav-Container .open .nav-links:hover,
.Nav-Container .dropdown:hover .dropbtn {
  color: white;
  background-color: #3685c3;
}

.Nav-Container .dropdown-content {
  display: none;
  position: fixed;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 8px 8px 20px 0px rgba(0, 0, 0, 0.35);
  z-index: 1333;
  border-radius: 6px;
  transition: 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.Nav-Container .dropdown-content .nav-links {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.Nav-Container .dropdown-content .nav-links:hover {
  background-color: #ddd;
  color: #3685c3;
}

.Nav-Container .dropdown:hover .dropdown-content {
  display: block;
}

.Nav-Container .Button button {
  color: #3685c3;
  border: none;
  outline: none;
  padding: 8.8px 24.5px;
  border-radius: 5px;
  border: 2px solid #3685c3;
  transition: 0.3s ease-in;
  cursor: pointer;
  background-color: white;
}

.Nav-Container .Button button:hover {
  color: white;
  background-color: #3685c3;
}

.Nav-Container .Button button:focus {
  border: 2px solid #3685c3;
  outline: none;
}

